import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios'; // axios 
import '../assets/css/auth.scss'; //  
import ios from '../assets/images/ios.png';
import google from '../assets/images/logogoogle.png';
import jwtDecode from 'jwt-decode';
import qs from 'qs';


const Login = () => {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const navigate = useNavigate();

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            const response = await axios.post('/superant/login', qs.stringify({
                username,
                password
            }), {
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded'
                }
            });

            //  // JWT   (  )
            //  const jwtToken = response.headers['authorization']?.split(' ')[1]; // "Bearer <token>"  
            //  console.log('JWT Token from Header:', jwtToken);
            //  if (!jwtToken) {
            //     jwtToken = response.data.accessToken; //  JWT  
            //     if (!jwtToken) {
            //         alert('JWT    .');
            //         return;
            //     }
            // }
            // console.log('JWT Token:', jwtToken);
            
            //  // JWT 
            //  const decodedToken = jwtDecode(jwtToken);
            //  console.log(' JWT:', decodedToken);
 
            //  // JWT        (: localStorage)
            //  localStorage.setItem('jwtToken', jwtToken);
            //  localStorage.setItem('userInfo', JSON.stringify(decodedToken));
 
            //  //   
            //  navigate('/home');
            
            if (response.status === 200) {
                console.log(username, password)
                console.log(' :', response.data);
                //      
                navigate('/home'); 
            }
        } catch (error) {
            if (error.response && error.response.data) {
                alert(error.response.data.message || '   .');
            } else {
                alert('   .');
            }
        }

    };

    return (
        <div className="auth-container">
            <div>
                <div className="auth-card">
                    <h1 className="auth-title">Superant</h1>
                    <form onSubmit={handleSubmit} className="form">
                        <div className="input-group">
                            <input
                                type="username"
                                id="username"
                                placeholder=" "
                                value={username}
                                onChange={(e) => setUsername(e.target.value)}
                                required="required"
                            />
                        </div>
                        <div className="input-group">
                            <input
                                type="password"
                                id="password"
                                placeholder=" "
                                value={password}
                                onChange={(e) => setPassword(e.target.value)}
                                required="required"
                            />
                        </div>
                        <button type="submit"></button>
                       
                    </form>

                    <div className="divider">
                        <span>or</span>
                    </div>
                    <div className='google-ios'>
                        <button type="button" className='google'>
                            <img src={google} alt="google"/>Continue with Google
                        </button>
                        <button type="button" className='apple'>
                            <img src={ios} alt="ios"/>Continue with Apple
                        </button>
                    </div>
                    <div className="signup-link-container">
                        <a href="/signup" className="signup-link">Superant  </a>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Login;