import React, {useState, useEffect} from "react";
import PropTypes from 'prop-types';
import {Button, Table} from "reactstrap";
import axios from 'axios';

// scss
import '../../assets/css/stockTable.scss';

const TableFO = ({
    data,
    handleChange,
    desiredWeights,
    handleWeightChange,
    totalDesiredWeight,
    currentTotalBalance,
    addEmptyRow,
    searchButton,
}) => {

    const formatCurrency = (amount) => {
        return new Intl
            .NumberFormat('ko-KR', {
                style: 'decimal',
                minimumFractionDigits: 2,
                maximumFractionDigits: 2
            })
            .format(amount);
    };

    const adjustWeights = (index, value) => {
        const newDesiredWeights = [...desiredWeights];
        newDesiredWeights[index] = Math.min(100, Math.max(0, value));

        handleWeightChange(index, newDesiredWeights[index]);
    };

    useEffect(() => {
        console.log(data)
    }, [data]);

    return (
        <Table className="custom-table">
            <thead>
                <tr>
                    <th rowSpan="2" className="th"></th>
                    <th rowSpan="2" className="th"></th>
                    <th colSpan="3"></th>
                    <th rowSpan="2" className="th"></th>
                    <th colSpan="3"></th>
                    <th rowSpan="2" className="th"></th>
                    <th rowSpan="2" className="option-button"></th>
                </tr>
                <tr>
                    <th className="th"></th>
                    <th className="th"> ($)</th>
                    <th className="th"> (%)</th>
                    <th className="th"> (%)</th>
                    <th className="th"> ($)</th>
                    <th className="th"></th>
                </tr>
            </thead>
            <tbody>
                {
                    data.map((item, index) => {
                        // console.log(index, item.name, item.quantity, item.price);  
                        const currentprice = item.price * item.quantity;

                        //  
                        const currentBalance = currentTotalBalance > 0
                            ? (currentprice / currentTotalBalance) * 100
                            : 0;

                        //    
                        const currentDesiredWeight = parseFloat(desiredWeights[index]) || 0;

                        //   
                        const rebalanceWeight = totalDesiredWeight > 0
                            ? (currentDesiredWeight / totalDesiredWeight) * 100
                            : 0;

                        //   
                        const desiredInvestment = currentTotalBalance * (rebalanceWeight / 100);

                        //   
                        const desiredQuantity = desiredInvestment / item.price;

                        //  
                        const quantityControl = desiredQuantity - item.quantity;

                        //    
                        const quantityControlStyle = quantityControl > 0
                            ? 'text-plus'
                            : 'text-minus'; //  ,  
                        const quantityControlValue = quantityControl > 0
                            ? `+${quantityControl.toFixed(2)}`
                            : quantityControl.toFixed(2);

                        return (
                            <tr key={item.id}>
                                <td>
                                    <input
                                        type="text"
                                        value={item.name}
                                        onChange={(e) => handleChange(index, 'name', e.target.value)}/>
                                </td>
                                <td className="money-expression">$ {formatCurrency(item.price)}</td>
                                <td>
                                    <input
                                        className="number"
                                        type="number"
                                        value={item.quantity}
                                        onChange={(e) => handleChange(index, 'quantity', e.target.value)}/>
                                </td>
                                <td>
                                    {formatCurrency(currentprice)}</td>
                                <td>{formatCurrency(currentBalance.toFixed(2))}
                                    %</td>
                                <td>
                                    <input
                                        type="number"
                                        min="0"
                                        max="100"
                                        value={desiredWeights[index]}
                                        onChange={(e) => adjustWeights(index, e.target.value)}/>
                                </td>
                                <td>{rebalanceWeight.toFixed(2)}%</td>
                                {/*    */}
                                <td>
                                    {formatCurrency(desiredInvestment)}</td>
                                {/*   */}
                                <td>{desiredQuantity.toFixed(2)}</td>
                                {/*   */}
                                <td className={quantityControlStyle}>{quantityControlValue}</td>
                                {/*    */}
                                {searchButton(item, index)}
                            </tr>
                        );
                    })
                }
                <tr>
                    <td className="td11" onClick={addEmptyRow}>
                        <h2> </h2>
                    </td>
                </tr>
            </tbody>
        </Table>
    );
};

TableFO.propTypes = {
    data: PropTypes.array.isRequired, // data prop 
    totalBalance: PropTypes.number.isRequired, // totalBalance prop 
    handleChange: PropTypes.func.isRequired, // handleChange prop 
    desiredWeights: PropTypes.array.isRequired, //   
    handleWeightChange: PropTypes.func.isRequired, //     
    totalDesiredWeight: PropTypes.number.isRequired, // totalDesiredWeight 
};

export default TableFO;
