import React, {useState, useEffect} from "react";
import PropTypes from 'prop-types';
import '../assets/css/home.scss';
import Modal from 'react-modal'; //   

// components
import StockTable from "./components/stockTable";
import ImageUpload from "./components/imageUpload";

// modal  
Modal.setAppElement('#root');

const Home = () => {
    const [isImageUploadVisible, setImageUploadVisible] = useState(true);
    const [stockData, setStockData] = useState(null);
    const [isModalOpen, setModalOpen] = useState(true); //   

    const handleSave = (data) => {
        setStockData(data);
        setImageUploadVisible(false);
        setModalOpen(false); //     
    };

    const handleReloadImageUpload = () => {
        setStockData(null);
        setImageUploadVisible(true);
        setModalOpen(true); //      
    };

    const closeModal = () => {
        setModalOpen(false); //  
    };

    useEffect(() => {
        const handleKeyDown = (event) => {
            if (event.key === 'Enter') {
                closeModal(); //      
            }
        };

        if (isModalOpen) {
            window.addEventListener('keydown', handleKeyDown);
        }

        return() => {
            window.removeEventListener('keydown', handleKeyDown);
        };
    }, [isModalOpen]); //      

    return (
        <div className="home-container">
            <Modal
                isOpen={isModalOpen}
                onRequestClose={closeModal}
                contentLabel=" "
                className="upload-info"
                overlayClassName="modal-overlay"
                shouldCloseOnOverlayClick={false}>
                <h2></h2>
                <p>, , ,  </p>
                <button className="info-button" onClick={closeModal}></button>
            </Modal>

            {
                isImageUploadVisible
                    ? (<ImageUpload onSave={handleSave}/>)
                    : (<StockTable Reload={handleReloadImageUpload} SD={stockData}/>)
            }
        </div>
    );
};

Home.propTypes = {
    classes: PropTypes.object
};

export default Home;
